import React, { useEffect, useState } from "react";
//import { useAlert } from "react-alert";

import customerService from "../../services/customerService";
import CustomerForm from "../CustomerForm";
import CustomerTable from "../CustomerTable";
import "./pages.css";

const Customer = () => {
    //customer-form-useState
    const [fullName, setFullName] = useState("");
    const [emailID, setEmailID] = useState("");
    const [balanceAmount, setBalanceAmount] = useState(0);
    //customer-table-useState
    const [customerList, setCustomerList] = useState([]);
    //customer-submit-alert

    //customer-form-submit-btn
    const onSubmitForm = async (e) => {
        e.preventDefault();
        try {
            await customerService.create({
                name: fullName,
                email_id: emailID,
                balance_amount: balanceAmount,
            });
            setFullName("");
            setEmailID("");
            setBalanceAmount(0);
        } catch (err) {
            console.error(err.message);
        }
    };
    //customer-form-props-event
    const onFullNameChange = (e) => {
        setFullName(e.target.value);
    };
    const onemailIDChange = (e) => {
        setEmailID(e.target.value);
    };
    const onbalanceAmountChange = (e) => {
        setBalanceAmount(e.target.value);
    };

    //customer-table-get-service--parse data
    const getCustomers = async () => {
        try {
            const response = await customerService.get("/");
            // console.log(response);
            setCustomerList(response.data);
        } catch (err) {
            console.error(err.message);
        }
    };
    //customer-table-useEffect
    useEffect(() => {
        getCustomers();
    }, []);

    return (
        <div className="customer-container">
            {/* customer-form */}
            <section className="customer-form">
                <CustomerForm
                    fullName={fullName}
                    onFullNameChange={onFullNameChange}
                    emailID={emailID}
                    onemailIDChange={onemailIDChange}
                    balanceAmount={balanceAmount}
                    onbalanceAmountChange={onbalanceAmountChange}
                    onSubmitForm={onSubmitForm}
                />
            </section>

            {/* customer-list */}
            <section className="customer-list">
                <CustomerTable
                    customerList={customerList}
                    //customerList={onCustomerListChange}
                />
            </section>
        </div>
    );
};

export default Customer;
