import React from "react";

const TransactionsTable = (props) => {
    return (
        <div className="table-container">
            <h1 className="heading-center">All Transactions</h1>
            <table className="content-table">
                <thead>
                    <tr>
                        <th>Transaction ID</th>
                        <th>Sender</th>
                        <th>Receiver</th>
                        <th>Transfer Amount</th>
                        <th>Date/Time</th>
                    </tr>
                </thead>
                <tbody id="transaction-table-body">
                    {props.TransactionList.map((transaction) => (
                        <tr key={transaction.transactions_id}>
                            <td>{transaction.transactions_id}</td>
                            <td>{transaction.tfrom}</td>
                            <td>{transaction.tto}</td>
                            <td>{transaction.transfer_amount}</td>
                            <td>{transaction.transfer_time}</td>
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    );
};

export default TransactionsTable;
