import React from "react";
import "./pages.css";
import banklogoone from "../../images/banklogoone.png";

const Home = () => {
    return (
        <div>
            <section className="home-box-welcome">
                <img
                    src={banklogoone}
                    alt="bank"
                    className="home-welcome-logo"
                />
                <p className="home-welcome">Welcome To</p>
                <p className="home-welcome">
                    <b>BANK OF SPARKSICA</b>
                </p>
            </section>
            <section className="home-box-links">
                <button
                    className="home-box-buttons"
                    onClick={(event) => (window.location.href = "/Customer")}
                >
                    Customer DETAILS
                </button>
                <button
                    className="home-box-buttons"
                    onClick={(event) =>
                        (window.location.href = "/Transactions")
                    }
                >
                    TRANSACTIONS DETAILS
                </button>
            </section>
        </div>
    );
};

export default Home;
