import React from "react";
import tsflogo from "../../images/tsf-logo.png";
import "./pages.css";

const About = () => {
    return (
        <div className="about-container">
            <section className="about-section">
                <div className="about-side-words">
                    <h1 className="about-title">
                        <b> The Sparks Foundation </b>
                    </h1>
                    <p className="about-p">
                        <b>
                            We connect students of all financial backgrounds
                            with experts. Knowledge sharing enables equal
                            opportunity for all.
                        </b>
                    </p>
                    <p className="about-p">
                        <b>
                            To inspire, motivate and encourage students to
                            learn, create and help build a better society.
                        </b>
                    </p>
                </div>
                <div>
                    <img
                        src={tsflogo}
                        className="about-tsf-logo"
                        alt="TSF LOGO"
                    />
                </div>
            </section>
            {/* <section>
                <h1>Website by Sohaib Kamal</h1>
            </section> */}
        </div>
    );
};

export default About;
