import React, { useState } from "react";
import { Link, Switch, Route } from "react-router-dom";

import "./Navbar.css";

import About from "../Pages/About";
import Customer from "../Pages/Customer";
import Transactions from "../Pages/Transactions";
import Home from "../Pages/Home";

import { menuItems } from "./Menuitems";

const Navbar = () => {
    const [BmenuClick, setBmenuClick] = useState(false);

    const handlleClick = () => {
        setBmenuClick(!BmenuClick);
    };

    return (
        <div>
            <nav className="navbar">
                <Link to="/" className="navbar-logo">
                    <i className="fa fa-university" aria-hidden="true"></i>
                    SPARKSICA
                </Link>
                <div className="menu-icon" onClick={handlleClick}>
                    <i
                        className={BmenuClick ? "fas fa-times" : "fas fa-bars"}
                    ></i>
                </div>
                <ul className={BmenuClick ? "menu-items active" : "menu-items"}>
                    {menuItems.map((item) => {
                        return (
                            <li key={item.id} className={item.cName}>
                                <Link to={item.path}>{item.title}</Link>
                            </li>
                        );
                    })}
                </ul>
                {/* <div className="burger">
                    <div className="line1"></div>
                    <div className="line2"></div>
                    <div className="line3"></div>
                </div> */}
            </nav>
            <Switch>
                <Route path="/Home">
                    <Home />
                </Route>
                <Route path="/about">
                    <About />
                </Route>
                <Route path="/Customer">
                    <Customer />
                </Route>
                <Route path="/Transactions">
                    <Transactions />
                </Route>
            </Switch>
        </div>
    );
};

export default Navbar;
