import { useState, useEffect } from "react";

import TransferService from "../../services/TransferService";
import TransacationForm from "../TransacationForm";
import TransactionsTable from "../TransactionTable";
import "./pages.css";

const Transactions = () => {
    const [TFrom, setTFrom] = useState("");
    const [TTo, setTTo] = useState("");
    const [TransferAmount, setTransferAmount] = useState(0);

    const [TransactionList, setTransactionList] = useState([]);

    const getTransactions = async () => {
        try {
            const response = await TransferService.get("/");
            console.log(TransactionList);
            setTransactionList(response.data);
        } catch (err) {
            console.error(err.message);
        }
    };
    useEffect(() => {
        getTransactions();
    }, []);

    //---Form---
    const onSubmitForm = async (e) => {
        e.preventDefault();
        try {
            await TransferService.create({
                from: TFrom,
                to: TTo,
                transfer_amount: TransferAmount,
            });
            setTFrom("");
            setTTo("");
            setTransferAmount(0);
        } catch (err) {
            console.error(err.message);
        }
    };
    const onTFromChange = (e) => {
        setTFrom(e.target.value);
    };
    const onTToChange = (e) => {
        setTTo(e.target.value);
    };
    const onTransferAmountChange = (e) => {
        setTransferAmount(e.target.value);
    };

    //---Table---

    return (
        <div className="transaction-container">
            <section className="transaction-form">
                <TransacationForm
                    TFrom={TFrom}
                    onTFromChange={onTFromChange}
                    TTo={TTo}
                    onTToChange={onTToChange}
                    TransferAmount={TransferAmount}
                    onTransferAmountChange={onTransferAmountChange}
                    onSubmitForm={onSubmitForm}
                />
            </section>

            <section className="transaction-list">
                <TransactionsTable TransactionList={TransactionList} />
            </section>
        </div>
    );
};

export default Transactions;
