import React from "react";
import { useAlert } from "react-alert";

const TransacationForm = (props) => {
    const alert = useAlert();
    return (
        <div>
            <h1 className="heading-center">New Transaction</h1>
            <form onSubmit={props.onSubmitForm}>
                <label>
                    From:
                    <input
                        type="text"
                        className="form-control"
                        id="T-From"
                        required
                        value={props.TFrom}
                        onChange={props.onTFromChange}
                        name="Sender Mail"
                    />
                </label>
                <label>
                    To:
                    <input
                        type="text"
                        className="form-control"
                        id="T-To"
                        required
                        value={props.TTo}
                        onChange={props.onTToChange}
                        name="Receiver Mail"
                    />
                </label>
                <label>
                    Transfer Amount:
                    <input
                        type="text"
                        className="form-control"
                        id="T-Transfer-Amount"
                        required
                        value={props.TransferAmount}
                        onChange={props.onTransferAmountChange}
                        name="Transfer Amount"
                    />
                </label>
                <button
                    type="submit"
                    onClick={() => {
                        alert.show("Transaction Successfully Completed!");
                    }}
                >
                    Submit
                </button>
            </form>
        </div>
    );
};

export default TransacationForm;
