import React from "react";
import { useAlert } from "react-alert";

const CustomerForm = (props) => {
    const alert = useAlert();

    return (
        <div>
            <h1 className="heading-center">New Customer</h1>
            <form onSubmit={props.onSubmitForm}>
                <label>
                    Full Name:
                    <input
                        type="text"
                        className="form-control"
                        id="FullName"
                        required
                        value={props.fullName}
                        onChange={props.onFullNameChange}
                        name="Full Name"
                    />
                </label>
                <label>
                    Email ID:
                    <input
                        type="text"
                        className="form-control"
                        id="emailID"
                        required
                        value={props.emailID}
                        onChange={props.onemailIDChange}
                        name="Email ID"
                    />
                </label>
                <label>
                    Balance Amount:
                    <input
                        type="text"
                        className="form-control"
                        id="BalanceAmount"
                        required
                        value={props.balanceAmount}
                        onChange={props.onbalanceAmountChange}
                        name="Balance Amount"
                    />
                </label>
                <button
                    type="submit"
                    onClick={() => {
                        alert.show("Customer Successfully Registered!");
                    }}
                >
                    Submit
                </button>
            </form>
        </div>
    );
};

export default CustomerForm;
